@import url('https://fonts.googleapis.com/css2?family=Encode+Sans+Condensed:wght@100;200;300;400;500;600;700;800;900&family=Libre+Franklin:ital,wght@0,100..900;1,100..900&display=swap');
*{
  margin: 0;
  position: 0;
  padding: 0;  
}

body{
  font-family: "Encode Sans Condensed", sans-serif;
  font-weight: 100;
  font-style: normal;
  background-color: rgb(255, 234, 0);
}
h1{
  color: red;
  text-align: center;
}
header{
  width: 100%;
  height: 120px;
  background-color: black;
  color: white;
text-align: center;
font-size: 20px;
line-height: 120px;
}
.box{
  width: 60%;
  height: 200px;
  background-color: orange;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;

}
h2{
text-align: center;
padding-top: 10px;
}
p{
  width: 87%;
  padding-left: 40px;
  padding-right: 20px;
  font-size: 20px;
  padding-top: 10px;
  color: white;
}
.titulo{
 
  color: white;
  text-align: center;
  font-weight: normal;
}


.list{
  width: 60%;
  height: 200px;
  background-color: orange;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}
.column-list {
  column-count: 3; /* Define el n√∫mero de columnas */
  column-gap: 20px; /* Espacio entre columnas */
  text-align: center;
  list-style: none;
  padding-top: 30px;
  color: black;
  font-weight: bold;
}

.column-list li {
  margin-bottom: 10px; /* Espacio entre elementos de la lista */
}
.text{
  text-align: center;
  padding-top: 30px;
}
.imgen{
  width: 60%;
  height: 500px;
  background-color: orange;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}
.write{
  width: 87%;
  padding-left: 45px;
  padding-right: 20px;
  font-size: 20px;
  padding-top: 10px;
  color: white;
}
.imgen{  
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 9px;
  
  
}
img{
  border-radius: 100%;
}
.formulario{
  width: 60%;
  height: 500px;
  background-color: orange;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
}
.formulario{
  width: 60%;
  height: 420px;
  background-color: orange;
}
.form1{
text-align: center;
padding-top: 40px;
color: black;
}

.formulario{
  width: 60%;
  height: 650px;
  background-color: orange;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.form_left{
  margin-left: 40px;
  
}
input{
  width: 90%;
  height: 40px;
  margin-top: 30px;
  margin-left: 20px;
}
label{
  left: 60px;
  position: absolute;
  padding-top: 10px;
}
textarea{
  width: 90%;
  margin-top: 30px;
  margin-left: 20px;
}
.btn{
  width: 50%;
  margin-left:200px;
  background-color: black;
  color: white;
  margin-top: 55px;
}
footer{
  margin-top: 60px;
  width: 100%;
  height: 80px;
  background-color: black;
  text-align: center;
  padding-top: 20px;
  margin-bottom: auto;
}
footer li{
  list-style: none;
  display: inline-block;
  font-size: 25px;
  color: white;
  margin: 12px;
}
.footer-icon a{
  color: white;
}
a:hover{
  color: orangered;
}
@media (max-width:1200px) {
  .box{
  width: 80%;
  height: 50%;
  margin-top: 50px;  
  }
  p{
    padding-bottom: 20px;
  }
  .list{
    width: 80%;
    height: 50%;
  }
  .column-list{
    column-count: 1;
  }
  .imgen{
    width: 80%;
    height:1600px;
  }
  img{
    width: 80%;
    height: 300px;
  }
  .formulario{
    width: 80%;
    height: 770px;
  }
  .form_left{
    margin-top: 30px;
    margin-left: 5px;
    margin-right: 10px;
  }
  label{
    left: 27px;
  }
  .btn{
    margin-left: 70px;
  }
}